@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
.linkContainer {
  margin-top: 10px;   
}

.linkContainer:first-child a {
  font-size: 14px;
  font-weight: 500;
}

.container{display:flex;align-items:center;margin:20px 30px}.instructionIcon{margin-right:20px}

.topPadding{padding-top:10px}

.HelperMessageContainer {
  margin-top: 27px;
}

@media screen and (max-width: 768px) and (min-width: 321px){
  .HelperMessageContainer {
    width: 340px;
  }
}

.HelperMessageContainer button > span {
  box-sizing: border-box;
  padding: 8px 0;
  font-size: 14px;
  line-height: 1.2;

  @include for-tablets-portrait-up {
    padding: 6px 0;
    line-height: 1.45;
  }

  @include for-tablets-landscape-up {
    font-size: 16px;
    line-height: 1.2;
  }
}

.HelperMessage .HelperButtons {
  margin-top: 16px;
}
