@import 'abstracts/variables';
@import 'abstracts/functions';
@import 'abstracts/mixins';

@import 'base/reset';
@import 'base/typography';

@import 'layout/grid';
@import url('https://fonts.googleapis.com/css?family=Roboto:400,500');

@import 'overrides/overrides';

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

/*Header*/

#root {
  margin: 0 auto;
	display:flex;
  flex-direction: column;
  flex-flow: column;
  min-height: 100vh;
  flex: 1;
}

.container-main {
  display: flex;
  flex-direction: column;
  flex-flow: column;
  justify-content: flex-start;
  padding-bottom: 80px;
}


.App {
  margin: 10px 0;
}

@media screen and (min-width: $viewport--md) {
  .quoteDisclosure1 {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: $viewport--md) {
  .quoteDisclosure1 {
    font-size: 16px;
    line-height: 1.5;
    white-space: pre;
  }
}

@media screen and (min-width: $viewport--md) {
  .quoteDisclosure2 {
    font-size: 16px;
    line-height: 1.5;
    text-align: left;
    color: $dark-grey;
    a {
      color: $main-blue;
    }
  }
}

@media screen and (max-width: $viewport--md) {
  .quoteDisclosure2 {
    font-size: 14px;
    line-height: 1.43;
    color: $dark-grey;

    a {
      color: $main-blue;
    }
  }
}

.titleCard {
  text-align: left;
  position: relative;
  white-space: pre;

  @include for-xs-only {
    height: 119px;
    padding-top: 6px;
  }

  @include for-sm-phones-up {
    height: 131px;
    padding-top: 6px;
  }

  @include for-tablets-portrait-up {
    height: 140px;
    padding-top: 13px;
    white-space: normal;
  }

  @include for-tablets-landscape-up {
    height: 172px;
    padding-top: 13px;
  }

  @include for-desktops-up {
    height: 115px;
    padding-top: 13px;
  }

  @include for-big-desktops-up {
    height: 196px;
    padding-top: 13px;
  }
}

.titleCard:after {
  background-repeat: no-repeat; 
  content: "";
  display: block; 
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  width: 100vw;
  background-position-x: center;
  background-position-y: 100%;
  z-index: -9999;
  
  @include for-xs-only {    
    background-size: contain;
    background-image: url('../assets/images/titleCardBackground/sm/group-12.png');
    margin-top: -72px; 
    height: 119px;
  }

  @include for-sm-phones-up {
    background-size: contain;
    background-image: url('../assets/images/titleCardBackground/sm/group-12.png');
    margin-top: -100px;  
    height: 140px;
  }

  @include for-sm-phones-up {
    background-size: contain;
    background-image: url('../assets/images/titleCardBackground/md/group-12@2x.png');
    margin-top: -100px;  
    height: 140px;
  }

  @include for-tablets-portrait-up {
    background-size: contain;
    background-image: url('../assets/images/titleCardBackground/md/group-12@2x.png');
    margin-top: -68px;
    height: 155px;
  }

  @include for-tablets-landscape-up {
    background-image: url('../assets/images/titleCardBackground/xl/group-13.png');
    margin-top: -100px;  
    height: 180px;
  }

  @include for-desktops-up {
    background-image: url('../assets/images/titleCardBackground/xl/group-13.png');
    margin-top: -70px;  
    height: 115px;
    background-repeat: repeat-x; 
  }

  @include for-big-desktops-up {
    background-image: url('../assets/images/titleCardBackground/xl/group-13.png');
    height: 196px;
    margin-top: -100px;  
    background-repeat: no-repeat; 
  }
}

.MuiContainer-root, .u-flex-col-wrapper {
  display: flex;
  flex-direction: column;
  flex-flow: column;
  flex: 1;
}

.main-content-width {
  max-width: 534px;
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width: 767px) { 
  .u-mobile-flex-wrapper {
    justify-content: space-between;
    align-items: stretch;
  }
}

.HelperMessageContainer {
  margin-top: 27px;
}

.HelperMessageContainer button > span {
  box-sizing: border-box;
  padding: 8px 0;
  font-size: 14px;
  line-height: 1.2;

  @include for-tablets-portrait-up {
    padding: 6px 0;
    line-height: 1.45;
  }

  @include for-tablets-landscape-up {
    font-size: 16px;
    line-height: 1.2;
  }
}


.storyboard {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  width:100%;
  height:calc(100vh);

  .HelperMessageContainer button > span {
    font-size: 14px;
  }

  .MuiFormControl-root {
    width: 100% !important;
  }

  .flow-selector {
    padding: 10px;
  }

  .dispatch-box {
    position: absolute;
    bottom: 0px;
    width: 100%;
    display: flex;
    textarea {
      width: 100%;
    }
  }

  .flow-link, .flow-link-selected {
    cursor: pointer;
  }

  .flow-link-selected {
    color: white;
    background-color: #CCC;
  }

  .react-flow__node-default {
    width: 410px;
    height: 600px;
    border: 1px solid #000;
    overflow: hidden scroll;
    padding: 0px !important;
    text-align: left;
  }

  .react-flow__node-default[data-id='sorry'], .react-flow__node-default[data-id='END_OF_FLOW'] {
    width: 200px;
    height: 200px;
    border: 5px solid #000;
    overflow: hidden;
    padding: 0px !important;
    text-align: center;
    border-radius: 100px;
    line-height: 200px;
    font-size: 25px;
  }

  .react-flow__node-default[data-id='sorry'] {
    background-color: red;
  }
  
  .react-flow__node-default[data-id='END_OF_FLOW'] {
    background-color: greenyellow;
  }

  .react-flow__edge-path {
    stroke-width: 10px;
  }

  .react-flow__handle {
    opacity: 0;
  }
  
  .react-flow__handle-bottom {
    bottom: 0px;
  }

  .react-flow__handle-right {
    right: 0px;
  }

  .react-flow__edge-text {
    font-size: 18px;
  }

  .screen {
    display: block;
    width: 400px;
    max-height: 600px;
    overflow: hidden;
  }
}

.MuiContainer-root {
  width: 100% !important;
  margin-left: 0px !important;
  margin-right: 0px !important;
}

.container-main {
  padding-bottom: 0px !important;
}