@mixin for-xs-only {
    @media screen and (min-width: 320px) { @content; }
}

@mixin for-sm-phones-up {
    @media screen and (min-width: 360px) { @content; }
}

@mixin for-sm-phones-landscape-up {
    @media screen and (min-width: 568px) { @content; }
}

@mixin for-tablets-portrait-up {
    @media screen and (min-width: 768px) { @content; }
}

@mixin for-tablets-landscape-up {
    @media screen and (min-width: 1024px) { @content; }
}

@mixin for-desktops-up {
    @media screen and (min-width: 1200px) { @content; }
}

@mixin for-big-desktops-up {
    @media screen and (min-width: 1800px) { @content; }
}