// Font Variables
@import '../abstracts/variables';

.bold-text {
  font-weight: 700;
}

.darkGray {
  color: $dark-grey;
}

.body1 {
  font-family: Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.43;
}

.body2 {
  font-family: Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.43;
}

.CoveragePageTooltip {
  .MuiLink-root {
    font-size: 14px;
  }
}
// CustomizeCoverage

// Button Block
.CustomizeCoverageButtonBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  & > button {
    margin-bottom: 7px;
  }

  & .MuiButton-containedPrimary.MuiButton-root {
    margin-left: initial;
    margin-right: initial;
  }

  @media screen and (min-width: 768px) {
    flex-direction: row;

    .Button {
      width: 250px;
    }
  }
}

// Review Page
a#contactAssignedRiskPhone {
  font-size: inherit;
  font-weight: inherit;
}

// Footer
.CustomizeCoverageActionButton {
  width: 100%;
}

#customize-coverage-sticky-footer {
  height: auto;
  padding-left: 0;
  height: auto;
  padding-right: 0;

  .CustomizeCoverageFooterFieldGroup {
    @extend .main-content-width;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  
    & .Button {
      width: auto;
    }
  
    & .MuiButton-containedPrimary.MuiButton-root {
      margin-left: initial;
      margin-right: initial;
    }

    & .MuiButton-root {
      transform: translateY(0px);
    }
  
    @media screen and (min-width: 768px) {
      justify-content: center;
  
      & .Button {
        width: 314px;
      }
  
      & .MuiButton-containedPrimary.MuiButton-root {
        margin-right: 16px;
      }
    }
  }
}

.CustomizeCoverageDropdownLabel { 
  display: flex;
  flex-grow: 1;
  width: 65%;
  .MuiFormLabel-root {
    font-size: 16px;
    font-weight: 500
  }
}

.CustomizeCoverageDetailPremium { 
  flex-grow: 1;
  font-weight: 500;
  display: inline-block;
  text-align: right;
  margin-top: 10px;
  span {
    font-weight: 500;
  }
}

.CongratulationsRightAlign { 
  flex-grow: 1;
  font-weight: 500;
  display: inline-block;
  text-align: right;
  span {
    font-weight: 500;
  }
}

.BoxShadow { 
  padding: 10px;
  box-shadow: 0 0 3px dimgray;
  border-radius: 2px;
  margin-top: 5px;
}

.text-align-center {
  text-align: center;
}

@media screen and (max-width: 769px) { 
  .FlowBreakButton {
    width: 100%
  }
}

@media screen and (min-width: 768px) {
  .body1 {
    font-size: 18px;
    line-height: 1.5;
  }

  .body2 {
    font-size: 16px;
    line-height: 1.5;
  }

  .CoveragePageTooltip {
    .MuiLink-root {
      font-size: 16px;
    }
  }
}